.top-container {
  width: 100%;
  height: 785px;
  background-color: #010101;
  margin: 0;
  position: relative;
  overflow: hidden;
  padding-top: 20px;
}
.top-container:before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 75px;
  z-index: 1;
  background: linear-gradient(transparent, #00011c);
}
.bubble{
  position: absolute;
  bottom: 0;
  border-radius: 50%;
  opacity: 0.18;
  -webkit-animation: flying 8s infinite ease-in;
          animation: flying 8s infinite ease-in;
}
.bubble:nth-child(1){
  background: radial-gradient( #2aa8aa,#000000);
  width: 100px;
  height: 100px;
  left: 10%;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}
.bubble:nth-child(2){
